import React from "react";
import PropTypes from "prop-types";

const ProjectItem = ({ data, color }) => {
  const rand = Math.floor(Math.random() * color.length);
  return (
    <div className="col mb-3">
      <div
        className="card card-body shadow float-up"
        style={{
          backgroundColor: color[rand],
          width: "180px",
          height: "150px"
        }}
      >
        <a
          href={data.demo}
          target="_blank"
          rel="noopener noreferrer"
          style={{ color: "#333", textDecoration: "none", fontWeight: "bold" }}
        >
          {data.name}
        </a>
        <hr />
        <a
          href={data.github}
          className="text-dark"
          target="_blank"
          rel="noopener noreferrer"
        >
          <i className="fab fa-github fa-2x enlarge"></i>
        </a>
      </div>
    </div>
  );
};

ProjectItem.propTypes = {
  data: PropTypes.object.isRequired,
  color: PropTypes.array.isRequired
};

export default ProjectItem;
