import React, { Fragment } from "react";
import CVPDF from "../../mca_cv_en.pdf";
import AvatarImg from "../../avatar.jpg";

const About = () => {
  return (
    <Fragment>
      <div className="container text-center">
        <div className="row bg-light rounded-lg p-5 mb-4 shadow">
          <div className="col-md-6">
            <img src={AvatarImg} alt="avatar" style={{ width: "100%" }} />
          </div>
          <div className="col-md-6 text-justify">
            <p>
              I am <span className="text-secondary h">Mehmet Can Aygün</span>, a
              23 years old who has a strong passion and interest for web
              programming. I was born in Zonguldak / Turkey. I have been trying
              to learn web programming for a year. I like applying new skills
              that I learn to some small projects to practice. I like being
              organized and planned. I consider myself as a planned, creative,
              and self-motivated person. I am definitely obsessed with movies.
              My favorite genres are Sci-Fi, Fantastic, and Horror. I have
              always been a Marvel fan, and I like drawing superheros, and
              creating my own characters. I also enjoy games with a great story
              line.
            </p>
            <a
              href={CVPDF}
              className="btn btn-dark btn-block mt-2 mb-2"
              style={{ borderRadius: "0" }}
            >
              See my CV
            </a>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default About;
